<template>
  <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">
    <FormItem label="" prop="name">
      <Input v-model="formValidate.name" placeholder=""></Input>
    </FormItem>
    <FormItem label="" prop="mail">
      <Input v-model="formValidate.mail" placeholder=""></Input>
    </FormItem>
    <FormItem label="" prop="city">
      <Select v-model="formValidate.city" placeholder="">
        <Option value="beijing"></Option>
        <Option value="shanghai"></Option>
        <Option value="shenzhen"></Option>
      </Select>
    </FormItem>
    <FormItem label="">
      <Row>
        <Col span="11">
          <FormItem prop="date">
            <DatePicker type="date" placeholder="" v-model="formValidate.date"></DatePicker>
          </FormItem>
        </Col>
        <Col span="2" style="text-align: center">-</Col>
        <Col span="11">
          <FormItem prop="time">
            <TimePicker type="time" placeholder="" v-model="formValidate.time"></TimePicker>
          </FormItem>
        </Col>
      </Row>
    </FormItem>
    <FormItem label="" prop="gender">
      <RadioGroup v-model="formValidate.gender">
        <Radio label="male"></Radio>
        <Radio label="female"></Radio>
      </RadioGroup>
    </FormItem>
    <FormItem label="" prop="interest">
      <CheckboxGroup v-model="formValidate.interest">
        <Checkbox label=""></Checkbox>
        <Checkbox label=""></Checkbox>
        <Checkbox label=""></Checkbox>
        <Checkbox label=""></Checkbox>
      </CheckboxGroup>
    </FormItem>
    <FormItem label="" prop="desc">
      <Input v-model="formValidate.desc" type="textarea" :autosize="{minRows: 2,maxRows: 5}" placeholder="..."></Input>
    </FormItem>
    <FormItem>
      <Button type="primary" @click="handleSubmit('formValidate')"></Button>
      <Button @click="handleReset('formValidate')" style="margin-left: 8px"></Button>
    </FormItem>
  </Form>
</template>
<script>
export default {
  data () {
    var validateDesc = function (rule, value, callback) {
      if (value === '') {
        callback(new Error(''));
      } else if (!/^[a-zA-Z\u4e00-\u9fa5]+$/.test(value)) {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    return {
      formValidate: {
        name: '',
        mail: '',
        city: '',
        gender: '',
        interest: [],
        date: '',
        time: '',
        desc: ''
      },
      ruleValidate: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /^[a-zA-Z\u4e00-\u9fa5]+$/, message: '', trigger: 'blur' }
        ],
        mail: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'email', message: '', trigger: 'blur' }
        ],
        city: [
          { required: true, message: '', trigger: 'change' }
        ],
        gender: [
          { required: true, message: '', trigger: 'change' }
        ],
        interest: [
          { required: true, type: 'array', min: 1, message: '', trigger: 'change' },
          { type: 'array', max: 2, message: '', trigger: 'change' }
        ],
        date: [
          { required: true, type: 'date', message: '', trigger: 'change' }
        ],
        time: [
          { required: true, type: 'string', message: '', trigger: 'change' }
        ],
        desc: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'string', min: 20, message: '20', trigger: 'blur' }, 
          { validator: validateDesc, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.$Message.success('!');
          console.log(this.formValidate);
        } else {
          this.$Message.error('Fail!');
        }
      })
    },
    handleReset (name) {
      this.$refs[name].resetFields();
    }
  }
}
</script>



